import {
  LitComponent,
  html,
  definition,
  ifDefined,
} from '@eui/lit-component';
import style from './link.css';
/**
 * @property {String} href - link address.
 * @property {Boolean} subtle - display the link with subtle styling.
 */
@definition(`eui-base-v${__VERSION__}-link`, {
  style,
  props: {
    href: { attribute: true, type: String, default: '' },
    subtle: { attribute: true, type: Boolean, default: false },
    target: { attribute: true, type: String },
  },
})
export default class Link extends LitComponent {
  render() {
    return html`
      <a href=${this.href} target=${ifDefined(this.target)}>
        <slot></slot>
      </a>
    `;
  }
}
