/**
 * Component Infopopup is defined as
 * `eui-base-v[version]-info-popup`
 *
 * Imperatively create component
 * @example
 * let component = new InfoPopup();
 *
 * Declaratively create component
 * @example
 * <eui-base-v0-info-popup></eui-base-v0-info-popup>
 *
 * @extends {LitComponent}
 */
import {
 LitComponent, html, ifDefined, nothing, definition,
} from '@eui/lit-component';
import '@eui/theme';
import style from './infoPopup.css';

/**
 * @property {String} message - Message for the info popup.
 * @property {String} position - position of the info popup.
 * @property {String} icon - name of the icon to add to the info popup.
 */
@definition(`eui-base-v${__VERSION__}-info-popup`, {
  style,
  props: {
    message: { attribute: true, type: String },
    position: { attribute: true, type: String, default: 'right' },
    _visible: { type: String, default: 'never' },
    icon: { attribute: true, type: String, default: 'info' },
  },
})
export default class InfoPopup extends LitComponent {
  /**
   * handle click and mouseup events.
   * make info pop up visible.Add event listener (mouseup) to the document to detect
   * if the user clicks outside of the info pop up.
   * make info popup hidden. Remove event listener (mouseup) from the
   * document when the info popup is no longer visible.
   * @function handleEvent
   * @param {Event} event - event Object
   */

  handleEvent(event) {
    event.stopPropagation();

    if (event.type === 'click') {
      this._visible = this._visible === 'always' ? 'never' : 'always';
      if (this._visible === 'always') {
        document.addEventListener('mouseup', this);
      }
    }

    if (event.type === 'mouseup') {
      const hideDropdown = () => {
        this._visible = 'never';
        document.removeEventListener('mouseup', this);
      };

      if (this._visible === 'always') {
        try {
          if (event.path.indexOf(this) === -1) {
            hideDropdown();
          }
        } catch (error) {
          if (!this.contains(event.target)) {
            hideDropdown();
          }
        }
      }
    }
  }

  /**
   * Render the info-popup component. This function is called each time a
   * prop changes.
   */

  render() {
    return html`
    <eui-base-v0-tooltip id="tooltip" @click=${this} message=${ifDefined(this.message)} position=${this.position} visible=${this._visible}>
    ${(!this.message) ? html`<div slot="message"> <slot></slot> </div>` : nothing}
      <eui-v0-icon name=${this.icon} @click=${this}></eui-v0-icon>
    </eui-base-v0-tooltip>
    `;
  }
}
