/* eslint no-console:0 */
/**
 * Button represents a simple button component. It is defined as
 * `<eui-base-v[version]-button>`
 * Where [version] is replaced with the major version number of the package.
 * The version of the package is found in project.json.
 *
 * Imperatively create a button component
 * @example
 * let button = new Button();
 *
 * Declaratively create a button component
 * @example
 * <eui-base-v0-button>My Button</eui-base-v0-button>
 *
 * @extends {LitComponent}
 */
import {
  LitComponent,
  html,
  classMap,
  ifDefined,
  definition,
} from '@eui/lit-component';
import '@eui/theme';
import style from './button.css';
/**
 * @property {Boolean} alignEdge - true aligns the label and the icon (if present)
 * to the edges of the button
 * @property {Boolean} disabled - true disables the component, false enables the component
 * @property {String} download - set the name of the file to download
 * @property {Boolean} fullwidth -true, sets the button
 * @property {String} href - a href to be navigated to on click of the component
 * @property {String} icon - name of the icon to display in the button
 * @property {Soolean} reverse - true reverses the side of the icon
 * @property {Boolean} primary - true sets the button as the primary button
 * @property {String} target - target of the link button, used with href prop when
 * creating a link button
 * @property {Boolean} warning - true sets the button as a warning button
 */
@definition(`eui-base-v${__VERSION__}-button`, {
  style,
  props: {
    alignEdge: { attribute: true, type: Boolean },
    disabled: { attribute: true, type: Boolean },
    download: { attribute: true, type: String },
    fullwidth: { attribute: true, type: Boolean },
    href: { attribute: true, type: String },
    icon: { attribute: true, type: String },
    reverse: { attribute: true, type: Boolean },
    primary: { attribute: true, type: Boolean },
    target: { attribute: true, type: String },
    warning: { attribute: true, type: Boolean },
  },
})
export default class Button extends LitComponent {
  constructor() {
    super();
    this.addEventListener('click', this);
    this.addEventListener('focus', () => {
      // Shift focus to button field to avoid double tab
      this.shadowRoot.querySelector('.btn').focus();
      this._setTabindex(-1);
    });
  }

  /**
   * Set tabindex to component
   * @param {String|Number} tabindex for component
   */
  _setTabindex(tabindex) {
    if (tabindex === null) {
      this.removeAttribute('tabindex');
    } else {
      this.setAttribute('tabindex', tabindex);
    }
  }

  didUpgrade() {
    this.userDefinedTabIndex = this.getAttribute('tabindex');
    if (this.disabled) {
      this._setTabindex(-1);
    }
  }

  didChangeProps(changedProps) {
    if (changedProps.has('disabled')) {
      this._setTabindex(this.disabled ? -1 : this.userDefinedTabIndex);
    }
  }

  /**
   * If the button is disabled then stop propagation of the event
   * @param {Event} event - event from component.
   */
  handleEvent(event) {
    if (this.disabled) {
      event.preventDefault();
      event.stopImmediatePropagation();
    } else if (event.type === 'blur') {
      this._setTabindex(this.userDefinedTabIndex);
    }
  }

  /**
   * _isIcon determines if there is an icon added to the button.
   *
   * @function _isIcon
   * @private
   */
  _isIcon = () => this.icon && this.icon.length > 0;

  /**
   * _withIcon renders the markup for the icon if it is added to the button
   *
   * @function _withIcon
   * @private
   */
  _withIcon = () => {
    if (this._isIcon()) {
      return html`<eui-v0-icon name=${this.icon} size="14px"></eui-v0-icon>`;
    }
    return null;
  };

  _setClasses = () => classMap({
      btn: true,
      'btn--reverse': this.reverse,
      'btn--align--edge': this.alignEdge,
    });

  /**
   * Render markup for link
   *
   * @function _renderLink
   * @private
   */
  _renderLink = () => html`
    <a
      class="${this._setClasses()}"
      href="${this.href}"
      download=${ifDefined(this.download)}
      target="${ifDefined(this.target)}"
      ?disabled=${this.disabled}
      @blur=${this}
    >
      ${this.reverse ? '' : this._withIcon()}
      <span class="button__label ${this._isIcon() ? 'with--icon' : ''}">
        <slot></slot>
      </span>
      ${this.reverse ? this._withIcon() : ''}
    </a>
  `;

  /**
   * Render markup for the regular button
   *
   * @function _renderButton
   * @private
   */
  _renderButton = () => html`
    <button class="${this._setClasses()}" ?disabled=${this.disabled} @blur=${this}>
      ${this.reverse ? '' : this._withIcon()}
      <span class="button__label ${this._isIcon() ? 'with--icon' : ''}">
        <slot></slot>
      </span>
      ${this.reverse ? this._withIcon() : ''}
    </button>
  `;

  /**
   * Render the button component. This function is called each time a
   * prop changes.
   */
  render() {
    if (this.href) {
      return this._renderLink();
    }
    return this._renderButton();
  }
}
