const TOOLTIP_Y_SHIFT = 5;
const TOOLTIP_X_SHIFT = 19;
const TOOLTIP_X_PADDING = 5;
const TOOLTIP_Y_PADDING = 5;

export const positionMatcher = {
  'bottom-end': {
    check(componentRect, tooltipRect) {
      return window.innerHeight > componentRect.top + componentRect.height
          + tooltipRect.height + TOOLTIP_Y_PADDING
          && componentRect.left + componentRect.width / 2 - TOOLTIP_X_SHIFT - tooltipRect.width > 0;
    },
    next: 'bottom',
  },
  bottom: {
    check(componentRect, tooltipRect) {
      return window.innerHeight > componentRect.top + componentRect.height
          + tooltipRect.height + TOOLTIP_Y_PADDING
          && window.innerWidth > componentRect.left + componentRect.width / 2 - TOOLTIP_X_SHIFT
          + tooltipRect.width;
    },
    next: 'right',
  },
  right: {
    check(componentRect, tooltipRect) {
      return window.innerWidth > componentRect.left + componentRect.width
          + tooltipRect.width + TOOLTIP_Y_SHIFT
          && window.innerHeight > componentRect.top + componentRect.height / 2
          + tooltipRect.height - TOOLTIP_X_PADDING;
    },
    next: 'left',
  },
  left: {
    check(componentRect, tooltipRect) {
      return componentRect.left - TOOLTIP_Y_SHIFT - tooltipRect.width > 0
          && window.innerHeight > componentRect.top + componentRect.height / 2
          + tooltipRect.height + TOOLTIP_X_PADDING;
    },
    next: 'top-end',
  },
  'top-end': {
    check(componentRect, tooltipRect) {
      return componentRect.top - tooltipRect.height - TOOLTIP_Y_PADDING > 0
          && componentRect.left + componentRect.width / 2 - tooltipRect.width + TOOLTIP_X_SHIFT > 0;
    },
    next: 'top',
  },
  top: {
    check(componentRect, tooltipRect) {
      return componentRect.top - tooltipRect.height - TOOLTIP_Y_PADDING > 0
          && window.innerWidth > componentRect.left + componentRect.width / 2
          + tooltipRect.width + TOOLTIP_X_SHIFT;
    },
    next: 'bottom-end',
  },
};
