/**
 * Component Accordion is defined as
 * `<eui-base-v[version]-accordion>`
 *
 * Imperatively create component
 * @example
 * let component = new Accordion();
 *
 * Declaratively create component
 * @example
 * <eui-base-v[version]-accordion></eui-base-v[version]-accordion>
 *
 * @extends {LitComponent}
 */
import { LitComponent, html, definition } from '@eui/lit-component';
import '@eui/theme';
import style from './accordion.css';

/**
 * @property {Boolean} line - true adds a line above and below the accordion
 * @property {String} categoryTitle - the title of the accordion
 * @property {Boolean} open - true opens the accordion, false closes the accordion
 */
@definition(`eui-base-v${__VERSION__}-accordion`, {
  style,
  props: {
    categoryTitle: { attribute: true, type: String, default: 'Title' },
    line: { attribute: true, type: Boolean },
    open: { attribute: true, type: Boolean },
  },
})
export default class Accordion extends LitComponent {
  /**
   * handle a click on the accordion title.
   * @method _handleAccordionClick
   *
   * @private
   */
  _handleAccordionClick = () => {
    this.open = !this.open;
  }

  /**
   * If the accordion is part of a group of accordions then the margin must be different
   * depending on the line set to true/false. If the line is set to display, then it
   * must adjust it's margin so two stacked accordions look like there is only a line
   * of 1px between them and not a line of 2px.
   *
   * @function _previousSibling
   * @private
   */
  _previousSibling = () => {
    if (this.previousElementSibling && this.previousElementSibling.localName === `eui-base-v${__VERSION__}-accordion`) {
      if (this.line) {
        return 'accordion__item--sibling';
      }
    }
    return '';
  }

  /**
   * Render the <eui-base-v[version]-accordion> component. This function is called each time a
   * prop changes.
   *
   * @method render
   */
  render() {
    const divTitle = html`
      <div class="accordion__item__title" @click=${this._handleAccordionClick}>
        <div class="left-slot">
          <slot name="left"></slot>
        </div>
        <span class="title">${this.categoryTitle}</span>
        <div class="right-slot">
          <slot name="right"></slot>
          <eui-v0-icon id="accordion-chevron" name=${this.open ? 'chevron-up' : 'chevron-down'} size="16px"></eui-v0-icon>
        </div>
      </div>`;

    const divContent = html`
      <div part="content" class="accordion__item__content">
        <slot></slot>
      </div>`;

    return html`
      <div class="accordion" tabindex="0">
        <div class="accordion__item ${this._previousSibling()} ${this.open ? 'accordion__item--opened' : 'accordion__item--closed'
            }">
          ${divTitle}
          ${divContent}
        </div>
      </div>`;
  }
}
