import { TemplateComponent, definition } from '@eui/component';
import '@eui/theme';
import template from './loader.html';
import style from './loader.css';

@definition(`eui-base-v${__VERSION__}-loader`, {
  style,
  template,
  props: {
    hidden: { attribute: true, type: Boolean },
    size: { attribute: true, type: String, default: 'medium' },
    color: { attribute: true, type: String },
  },
})
export default class Loader extends TemplateComponent {
  /**
   *
   * @function didConnect
   * @description lifecycle hook
   * triggers when component connected to DOM.
   */
  didConnect() {
    this.loaderIcon = this.shadowRoot.getElementById('loaderIcon');
  }

  didUpgrade() {
    if (this.color) {
      this.loaderIcon.color = this.color;
    }
  }

  /**
   *
   * @function didChangeProps
   * @description lifecycle hook
   * triggered when component received props
   */
  didChangeProps(changedProps) {
    if (changedProps.has('color') && this.color) {
      this.loaderIcon.color = this.color;
    }
  }
}
